/************************************************
 * Variables
 */
@font-face {
  font-family: 'DinLight';
  src: url('../../fonts/din-light/din-light.eot');
  src: url('../../fonts/din-light/din-light.eot?#iefix') format('embedded-opentype'), url('../../fonts/din-light/din-light.woff') format('woff'), url('../../fonts/din-light/din-light.ttf') format('truetype'), url('../../fonts/din-light/din-light.svg#DinLight') format('svg');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'DinRegular';
  src: url('../../fonts/din-regular/din-regular.eot');
  src: url('../../fonts/din-regular/din-regular.eot?#iefix') format('embedded-opentype'), url('../../fonts/din-regular/din-regular.woff') format('woff'), url('../../fonts/din-regular/din-regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'DinMedium';
  src: url('../../fonts/din-medium/din-medium.eot');
  src: url('../../fonts/din-medium/din-medium.eot?#iefix') format('embedded-opentype'), url('../../fonts/din-medium/din-medium.woff') format('woff'), url('../../fonts/din-medium/din-medium.ttf') format('truetype'), url('../../fonts/din-medium/din-medium.svg#DinMedium') format('svg');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'DinBold';
  src: url('../../fonts/din-bold/din-bold.eot');
  src: url('../../fonts/din-bold/din-bold.eot?#iefix') format('embedded-opentype'), url('../../fonts/din-bold/din-bold.woff') format('woff'), url('../../fonts/din-bold/din-bold.ttf') format('truetype'), url('../../fonts/din-bold/din-bold.svg#DinBold') format('svg');
  font-weight: normal;
  font-style: normal;
}
html,
body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
}
body {
  font-family: 'DinLight', Arial, sans-serif !important;
  color: #666;
  /* disable text selection */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: moz-none;
  -ms-user-select: none;
  user-select: none;
}
.selectable_text {
  /* enable text selection */
  -webkit-touch-callout: text;
  -webkit-user-select: text;
  -khtml-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
}
input,
button,
select,
textarea {
  font-family: 'DinLight', Arial, sans-serif !important;
}
#lighting_designer {
  width: 100%;
  height: 100%;
}
#designer {
  width: 100%;
  height: 100%;
}
input {
  z-index: 1100 !important;
  position: relative;
}
a {
  color: #666;
}
a:hover {
  text-decoration: none;
}
*:focus {
  outline: 0;
}
/************************************************
 * Buttons
 */
.btn {
  padding: 0px;
  border: solid #ccc 1px;
  border-radius: 0px;
  text-align: center;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  text-shadow: none;
}
.btn_green {
  padding: 8px;
  min-width: 60px;
}
.active .btn_green {
  background-color: #009933 !important;
  color: white;
}
/************************************************
 * Fields
 */
.line_field {
  border-bottom: solid #ccc 1px;
  padding: 8px 0px 8px 0px;
}
.line_field .colour {
  width: 14px;
  height: 14px;
  display: inline-block;
  vertical-align: middle;
  border-radius: 50%;
}
.line_field .field_label {
  display: inline-block;
  vertical-align: middle;
}
.line_field .field_value {
  float: right;
}
/************************************************
 * Select
 */
select {
  color: #404040;
  background: white url('/img/icons/dropdown_arrow.png') no-repeat center right;
  -webkit-appearance: none;
  border: none;
  padding: 0px 30px 0px 0px;
  margin: 0px;
  min-width: 100px;
  border-radius: 0px;
  line-height: 18px;
  cursor: pointer;
}
select:focus {
  outline: none;
}
select:hover {
  background-color: #f1f1f1;
}
.text_center {
  text-align: center;
}
/************************************************
 * Loading style
 */
.loading {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  background: rgba(255, 255, 255, 0.9) url('/img/loading.png') no-repeat center center;
}
/************************************************
 * Collapsibles
 */
.collapsible .collapsible_header {
  display: block;
  background: #f1f1f1 url('/img/icons/dropdown_arrow.png') no-repeat center right;
  border: solid #ccc 1px;
  color: #009933;
  padding: 5px;
}
.collapsible .collapsible_header.no_border {
  border-top: none;
}
.collapsible .collapsible_header:hover {
  background-color: #e6e6e6 !important;
}
.collapsible .collapsible_content {
  display: none;
}
.collapsible.expanded .collapsible_header {
  background-image: url('/img/icons/dropdown_arrow_up.png');
}
.collapsible.expanded .collapsible_content {
  display: block;
}
/************************************************
 * Selectable
 */
.selectable {
  margin: 0px;
}
.selectable li {
  vertical-align: top;
  cursor: pointer;
  display: inline-block;
  margin-right: 0px;
  margin-bottom: 5px;
  list-style: none;
  border: solid transparent 1px;
  border-radius: 4px;
}
.selectable li a {
  display: inline-block;
  text-align: center;
  color: #333;
}
.selectable li:hover,
.selectable li.active {
  border: solid #ccc 1px;
}
.selectable li.active {
  background-color: #ebebeb;
  background: -webkit-gradient(linear, left top, left bottom, from(#ebebeb), to(white));
  background: -webkit-linear-gradient(top, #ebebeb, white);
  background: -moz-linear-gradient(top, #ebebeb, white);
  background: -ms-linear-gradient(top, #ebebeb, white);
  background: -o-linear-gradient(top, #ebebeb, white);
}
/************************************************
 * Tooltip
 */
#tooltip {
  position: absolute;
  width: 680px;
  top: 20px;
  padding: 10px;
  font-size: 11pt;
  font-weight: bold;
  text-align: center;
  font-size: 12px;
}
#tooltip_disable {
  float: right;
  margin-right: 15px;
  color: #2269F3;
}
/*
* This is for the full screen loader when communicating with the server
*/
#loading-screen {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 2000;
  margin: -100px 0 0;
  opacity: 0.8;
  background-color: black;
  display: none;
}
#loading-screen-message {
  text-align: center;
  margin-top: 10%;
  color: white;
}
#loading-screen-text {
  font-weight: bold;
  font-size: 14pt;
  margin-bottom: 5px;
}
#loading-screen-progress {
  width: 300px;
  margin-left: auto;
  margin-right: auto;
}
/*
* This is for the full screen error message
*/
#error-screen {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 2001;
  opacity: 0.8;
  background-color: red;
  display: none;
}
#error-screen-message {
  text-align: center;
  margin-top: 10%;
  color: white;
}
#error-screen-text {
  font-weight: bold;
  font-size: 14pt;
  margin-bottom: 5px;
}
#error-screen-detail {
  font-weight: bold;
  font-size: 12pt;
  margin-bottom: 5px;
}
.small {
  font-size: 10px;
}
:focus {
  outline: none;
}
.small_pad {
  padding: 10px;
}
/************************************************
 * Header
 */
#title_bar {
  height: 40px;
}
#title_bar #lp_logo {
  margin: 0px 20px;
}
#title_bar #bg_logo {
  height: 25px;
  margin: 8px 20px;
  float: right;
}
#title_bar #title_bar_right {
  float: right;
  margin: 10px 10px;
}
#title_bar #profile {
  display: inline-block;
}
#title_bar #share {
  display: inline-block;
}
/************************************************
 * Top bar
 */
#top_bar {
  background-color: #f1f1f1;
  border: solid #ccc 1px;
  height: 58px;
  min-width: 1140px;
}
#menu_bar {
  float: left;
  width: 1020px;
  list-style-type: none;
  margin: 0px 0px 0px 10px;
}
#menu_bar li {
  margin: 3px 5px 3px 5px;
  padding: 0px;
  float: left;
  vertical-align: top;
  border: solid #f1f1f1 1px;
  border-radius: 4px;
}
#menu_bar li a {
  font-size: 8pt;
  display: inline-block;
  width: 60px;
  text-align: center;
  padding-top: 35px;
  line-height: 10px;
  padding-bottom: 5px;
  border: none;
  background: transparent none no-repeat center 3px;
}
#menu_bar li.active {
  border: solid #ccc 1px;
  background-color: #e2e2e2;
  background: -webkit-gradient(linear, left top, left bottom, from(#e2e2e2), to(#f6f6f6));
  background: -webkit-linear-gradient(top, #e2e2e2, #f6f6f6);
  background: -moz-linear-gradient(top, #e2e2e2, #f6f6f6);
  background: -ms-linear-gradient(top, #e2e2e2, #f6f6f6);
  background: -o-linear-gradient(top, #e2e2e2, #f6f6f6);
}
#menu_bar li:hover {
  text-decoration: none;
  border: solid #ccc 1px;
  background-color: #f1f1f1;
}
#menu_bar li:hover a {
  text-decoration: none;
}
#menu_bar #menu_new a {
  background-image: url('/img/icons/new.png');
}
#menu_bar #menu_load a {
  background-image: url('/img/icons/load.png');
}
#menu_bar #menu_save a {
  background-image: url('/img/icons/save.png');
}
#menu_bar #menu_export a {
  background-image: url('/img/icons/export_pdf.png');
}
#menu_bar #menu_share a {
  background-image: url('/img/icons/share.png');
}
#menu_bar #menu_help a {
  background-image: url('/img/icons/help.png');
}
#menu_bar #menu_select a {
  background-image: url('/img/icons/arrow.png');
}
#menu_bar #menu_zoomin a {
  background-image: url('/img/icons/zoomin.png');
}
#menu_bar #menu_zoomout a {
  background-image: url('/img/icons/zoomout.png');
}
#menu_bar #menu_pan a {
  background-image: url('/img/icons/move_canvas.png');
}
#menu_bar #menu_fit a {
  background-image: url('/img/icons/fullscreen.png');
}
#menu_bar #menu_eraser a {
  background-image: url('/img/icons/eraser.png');
}
#menu_bar #menu_logout a {
  background-image: url('/img/icons/logout.png');
}
#menu_bar #menu_opacity {
  font-size: 8pt;
  display: inline-block;
  width: 85px;
  text-align: center;
  padding-top: 35px;
  line-height: 10px;
  padding-bottom: 5px;
  border: none;
  color: #333;
  background: transparent none no-repeat center 3px;
}
#menu_bar #opacity_slider {
  margin: -14px 0 8px;
  height: 0.4em;
  background: #666;
}
#menu_bar #opacity_slider a {
  width: 13px;
  height: 13px;
  padding: 0;
  margin-top: -3px;
  border-radius: 13px;
  background-color: #fff;
  border: 1px solid #ccc;
  opacity: 1;
  cursor: pointer;
}
#menu_bar #opacity_slider a span {
  position: absolute;
  margin: -14px 0 0 -8px;
}
#menu_bar #menu_help,
#menu_bar #menu_share {
  border-left: solid #ccc 1px;
  margin-left: 30px;
}
#menu_bar #menu_logout {
  position: absolute;
  right: 15px;
}
#menu_bar li.disabled a {
  cursor: default;
  opacity: 0.4;
  background-color: #f1f1f1 !important;
}
#menu_bar #menu_opacity.disabled {
  cursor: default;
  opacity: 0.4;
  background-color: #f1f1f1 !important;
}
/* space selector */
#space_selector_container {
  display: none;
  float: right;
  width: 180px;
  padding: 1px 10px;
  margin-right: 90px;
}
#space_selector_container label {
  display: block;
  line-height: 25px;
}
#space_selector_container select {
  width: 100%;
  border: solid #ccc 1px;
  padding: 2px;
}
#healthcheck textarea {
  height: 370px;
  width: 480px;
  margin: 5px 0px 0px 5px;
}
#healthcheck_link {
  font-size: 13px;
  color: #009933;
  margin: 10px 25px 0px 0px;
  display: inline-block;
}
#message {
  position: absolute;
  left: 50%;
  top: -500px;
  width: 400px;
  margin-left: -201px;
  background: #fff;
  border: 1px solid #e6e6e6;
  border-radius: 0 0 4px 4px;
  z-index: -1;
}
#message.active {
  top: -1px;
  z-index: 2000;
  -webkit-transition: top 1000ms cubic-bezier(0.075, 0.82, 0.165, 1);
  -moz-transition: top 1000ms cubic-bezier(0.075, 0.82, 0.165, 1);
  -o-transition: top 1000ms cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: top 1000ms cubic-bezier(0.075, 0.82, 0.165, 1);
}
#message h3 {
  color: #009933;
  font-weight: normal;
  padding: 5px 20px;
}
#message .text {
  border-top: 1px solid #e6e6e6;
  padding: 20px;
}
#message .dismiss {
  display: inline-block;
  float: right;
  padding: 0 20px 20px;
}
/************************************************
 * Designer
 */
#designer {
  width: 100%;
  height: 100%;
  background: white url('/img/grid.png') repeat top left;
}
/************************************************
  * Navigation bar
  */
#navigation_bar {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 20px;
  list-style-type: none;
  margin: 0px;
  padding: 0px;
}
#navigation_bar li {
  margin: 0px;
  padding: 0px;
  float: left;
  vertical-align: top;
}
#navigation_bar li a {
  font-size: 8pt;
  display: inline-block;
  width: 70px;
  text-align: center;
  padding-top: 43px;
  line-height: 10px;
  padding-bottom: 5px;
  border: none;
  background: transparent none no-repeat center 10px;
}
#navigation_bar li.active a,
#navigation_bar li a:hover {
  text-decoration: none;
}
#navigation_bar #tool_zoom_in a {
  background-image: url('/img/icons/zoomin.png');
}
#navigation_bar #tool_zoom_out a {
  background-image: url('/img/icons/zoomout.png');
}
#navigation_bar #tool_pan a {
  background-image: url('/img/icons/move_canvas.png');
}
#navigation_bar #tool_rotate a {
  background-image: url('/img/icons/rotate_canvas.png');
}
#navigation_bar #tool_fit a {
  background-image: url('/img/icons/fullscreen.png');
}
/************************************************
 * Drag asset (from sidebar)
 */
.drag_asset {
  /* above jquery dialogs */
  z-index: 10000000 !important;
  list-style-type: none;
  position: relative;
}
.drag_asset a {
  width: 54px;
  height: 54px;
}
.drag_asset .btn {
  border: none;
}
.drag_asset .img {
  width: 55px;
  margin-top: 0px;
  vertical-align: middle;
}
.drag_asset .img img {
  height: 35px;
  max-width: 50px;
}
.ui-draggable-dragging .info,
.ui-draggable-dragging .light_types {
  display: none;
}
.drag_asset .img {
  margin-top: 1px;
}
.drag_asset .txt {
  display: block;
  width: 100%;
  position: absolute;
  bottom: 2px;
}
.drag_asset:hover .img img {
  filter: alpha(opacity=100);
  -moz-opacity: 1;
  opacity: 1;
}
.drag_asset .txt {
  font-size: 10px;
  line-height: 9px;
}
body.hide_drag .ui-draggable-dragging {
  display: none !important;
}
li.drag_asset_selected .txt .type {
  color: white !important;
}
/************************************************
  * Canvas controls
  */
.canvas_tool {
  display: none;
  position: absolute;
  list-style-type: none;
  margin: 0px;
  left: 0px;
}
.canvas_tool li {
  display: inline-block;
  border: solid #ccc 2px;
  background-color: white;
  -webkit-transition: 0.1s linear all;
  -moz-transition: 0.1s linear all;
  -ms-transition: 0.1s linear all;
  -o-transition: 0.1s linear all;
  transition: 0.1s linear all;
}
.canvas_tool_vertical li {
  display: block;
  margin-bottom: 3px;
}
.canvas_tool li.active {
  background-color: #e7e7e7;
  background: -webkit-gradient(linear, left top, left bottom, from(#e7e7e7), to(white));
  background: -webkit-linear-gradient(top, #e7e7e7, white);
  background: -moz-linear-gradient(top, #e7e7e7, white);
  background: -ms-linear-gradient(top, #e7e7e7, white);
  background: -o-linear-gradient(top, #e7e7e7, white);
}
.canvas_tool li a {
  display: block;
  width: 30px;
  height: 30px;
  background: transparent url('/img/icons/single.png') no-repeat center center;
  text-align: left;
  border: none;
}
.canvas_tool li .text {
  display: inline-block;
  width: 30px;
  position: absolute;
  font-size: 10px;
  text-align: center;
  margin-top: -15px;
}
/* room tools */
.canvas_tool #light_tool_point a {
  background-image: url('/img/icons/single.png');
}
.canvas_tool #light_tool_line a {
  background-image: url('/img/icons/grid_line.png');
}
.canvas_tool #light_tool_grid a {
  background-image: url('/img/icons/grid_square.png');
}
.canvas_tool #light_tool_round a {
  background-image: url('/img/icons/grid_circle.png');
}
.canvas_tool #room_tool_settings a {
  background-image: url('/img/icons/preference.png');
}
.canvas_tool #room_tool_zoom a {
  background-image: url('/img/icons/fit_room.png');
}
/* light tools */
.canvas_tool #light_tool_link a {
  background-image: url('/img/icons/link.png');
}
.canvas_tool #light_tool_unlink a {
  background-image: url('/img/icons/unlink.png');
}
.canvas_tool #light_tool_add a {
  background-image: url('/img/icons/add.png');
}
.canvas_tool #light_tool_remove a {
  background-image: url('/img/icons/remove.png');
}
.canvas_tool #light_tool_rotate a {
  background-image: url('/img/icons/rotate.png');
}
/* asset tools */
.canvas_tool #asset_tool_link a {
  background-image: url('/img/icons/link.png');
}
.canvas_tool #asset_tool_unlink a {
  background-image: url('/img/icons/unlink.png');
}
.canvas_tool #asset_tool_rotate a {
  background-image: url('/img/icons/rotate.png');
}
.canvas_tool #asset_tool_note a {
  background-image: url('/img/icons/notes.png');
}
.canvas_tool #asset_tool_flip a {
  background-image: url('/img/icons/mirror.png');
}
/************************************************
 * Collapsible
 */
.ui-dialog .ui-dialog-button-collapse {
  height: 18px;
  margin: -10px 0 0;
  padding: 1px;
  position: absolute;
  right: 7px;
  top: 50%;
  width: 16px;
  border: solid #ccc 1px;
}
.ui-dialog .ui-dialog-button-collapse span {
  background-position: -48px -128px;
}
.ui-dialog .ui-dialog-button-collapse:hover {
  background-color: #f1f1f1;
}
.ui-dialog-collapsed .ui-dialog-titlebar {
  border-bottom: none;
}
.ui-dialog-collapsed .ui-dialog-content {
  display: none;
}
.ui-dialog-collapsed .ui-dialog-button-collapse span {
  /* plus icon */
  background-position: -16px -128px;
}
.ui-widget-content {
  border-color: #ccc;
}
/* dialog large padding title */
.dialog_large .ui-dialog-titlebar .ui-dialog-title {
  margin: 6px 0px;
}
/* add room, add furnature, lighting design */
.dialog_create {
  overflow: visible;
}
.dialog_create .ui-dialog-title {
  color: #009933 !important;
}
#create_panel_tabs {
  position: absolute;
  margin: -34px 0px 0px 4px;
  list-style-type: none;
}
#create_panel_tabs li {
  margin: 0px;
  padding: 0px;
  float: left;
  vertical-align: top;
}
#create_panel_tabs li a {
  background: white none no-repeat center center;
  padding: 8px 10px;
  margin-right: 4px;
  width: 27px;
  text-indent: -10000px;
  border: solid #ccc 1px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
#create_panel_tabs li.active a {
  border-bottom-color: white;
}
#create_panel_tabs li.active a:hover {
  border-bottom-color: #e6e6e6;
}
#create_panel_tabs #add_rooms a {
  background-image: url('/img/icons/create.png');
}
#create_panel_tabs #add_furniture a {
  background-image: url('/img/icons/furniture.png');
}
#create_panel_tabs #add_lights a {
  background-image: url('/img/icons/lighting_designer.png');
}
#create_panel_tabs #add_rooms.active a {
  background-image: url('/img/icons/create_selected.png');
}
#create_panel_tabs #add_furniture.active a {
  background-image: url('/img/icons/furniture_selected.png');
}
#create_panel_tabs #add_lights.active a {
  background-image: url('/img/icons/lighting_designer_selected.png');
}
/************************************************
 * Add furniture
 */
#add_furniture_dialog .drag_asset {
  margin-bottom: 13px;
}
#add_furniture_dialog .drag_asset .txt {
  font-size: 9px;
  line-height: 9px;
}
/************************************************
 * Add rooms
 */
#add_room_tools {
  margin-top: 20px;
}
#add_room_tools .btn {
  width: 65px;
  height: 65px;
  border: none;
}
#add_room_tools .img {
  margin-top: 5px;
}
#add_room_tools .txt {
  margin-top: 12px;
}
#room_select {
  border-right: solid #ccc 1px;
  margin-right: 15px;
}
/************************************************
 * Add lights
 */
#series_btn_list {
  list-style-type: none;
  margin-left: 0;
}
#series_btn_list li {
  display: inline-block;
}
#series_btn_list li a.btn_green {
