  display: block;
  position: relative;
  cursor: pointer;
  outline: none;
  user-select: none;
  margin: 0 auto;
  border-radius: 4px;
}
#share_dialog input.toggle + label {
  padding: 2px;
  width: 50px;
  height: 20px;
  background-color: #dddddd;
}
#share_dialog input.toggle + label:before,
#share_dialog input.toggle + label:after {
  display: block;
  position: absolute;
  top: 1px;
  left: 1px;
  bottom: 1px;
  content: "";
  border-radius: 4px;
}
#share_dialog input.toggle + label:before {
  right: 1px;
  background-color: #f1f1f1;
  transition: background 0.4s;
}
#share_dialog input.toggle + label:after {
  width: 25px;
  background-color: #fff;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.3);
  transition: margin 0.4s;
}
#share_dialog input.toggle:checked + label:before {
  background-color: #00a950;
}
#share_dialog input.toggle:checked + label:after {
  margin-left: 27px;
}
